import { FormGroup } from '@angular/forms';
import { EnderecoDTO } from './../models/domain/endereco.dto';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';

@Injectable()
export class ConsultaCEPService {

  constructor(private http: HttpClient) { }

  public consultarCEP(nrCep: string): Observable<any>  {
    nrCep = nrCep.replace(/\D/g, '');
    if (nrCep != "") {
      var validaCep = /^[0-9]{8}$/;
      if (validaCep.test(nrCep)) {
        return this.http.get<EnderecoDTO>(`//viacep.com.br/ws/${nrCep}/json/`);
      }
    }
  }

  private preencherFormEndereco(form: FormGroup, end: EnderecoDTO) {
    form.patchValue({
        endereco: {
          id: [],
          nrCep: end.nrCep,
          nmBairro: end.nmBairro,
          dsComplemento: end.dsComplemento,
          dsLogradouro: end.dsLogradouro,
          ufCidade: end.ufCidade,
          nmCidade: end.nmCidade,
        }
      });
    return form;
  }

  public preencherEndereco(end: any): EnderecoDTO {
    let ender = {} as EnderecoDTO;
    ender.nrCep = end['cep'];
    ender.nmBairro =end['bairro'],
    ender.dsComplemento = end['complemento'],
    ender.dsLogradouro = end['logradouro'],
    ender.ufCidade = end['uf'],
    ender.nmCidade = end['localidade'];
    return ender;
  }
}


